@charset "UTF-8";

/*файл кросбраузернности - в данном случае поддержка ie 9 */

.humans .items {
  margin-left: -16px;
  margin-right: -16px;
}

.humans .items::after {
  display: block;
  content: "";
  clear: both;
}

.humans .items .item {
  float: left;
}

@media screen and (max-width: 992px) {
  .humans .items .item:nth-child(odd) {
    clear: both;
  }
}

@media screen and (max-width: 576px) {
  .humans .items {
    margin-left: calc(8.33333% + 16px);
  }
}